import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { StayImage, StayCards, StayCard, ReviewBox, ReviewMarks } from './CardStyle';
// import { ColorText, InnerHeadings, InnerPara } from '../../../Common/Common';
import { ColorText, InnerHeadings, InnerPara } from '../../../../Common/Common';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
// import PlannerData from './Data/PData';



import './styles.css';

// import required modules
import { Navigation } from 'swiper/modules';
import SCardData from './SCardData';
function SCard() {
  const [swiperRef, setSwiperRef] = useState(null);

  return (
    // <StayCard className='cards' style={{width:"100%"}}>
      <Swiper
        onSwiper={setSwiperRef}
        slidesPerView={4}
        spaceBetween={30}
        navigation={true}
        modules={[Navigation]}
        className="mySwiper"
      >
        {SCardData.map((e) => (
          <SwiperSlide className='swip' style={{ width: "275.098px", height: "316px" }}>
            <StayImage src={e.img} />
            <InnerHeadings style={{padding:"10px"}}>
              {e.Room}
            </InnerHeadings>
            <ColorText style={{padding:"10px"}}>
              {e.Location}
            </ColorText>

            <ReviewBox>
              <ReviewMarks>
                <InnerPara>
                  {e.marks}
                </InnerPara>
              </ReviewMarks>
              <InnerPara>
                {e.feedBack}
              </InnerPara>
            </ReviewBox>

          </SwiperSlide>
        ))}
      </Swiper>
   
    // StayCard>
  );
}

export default SCard;
