import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { PlannerImage, PlannerCards, PlanCard } from './PcardStyle';
import { ColorText, InnerHeadings, InnerPara } from '../../../Common/Common';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import PlannerData from './Data/PlannerData';
import './styles.css';

// import required modules
import { Navigation } from 'swiper/modules';

function CardSlider() {
  const [swiperRef, setSwiperRef] = useState(null);

  return (
    <PlanCard>
      <Swiper
        onSwiper={setSwiperRef}
        slidesPerView={6}
        spaceBetween={30}
        navigation={true}
        modules={[Navigation]}
        className="mySwiper"
      >
        {PlannerData.map((e) => (
          <SwiperSlide className='swip' style={{ width: '170px', height: '250px',textAlign:"left" }}>
            <PlannerImage src={e.img} style={{height:"100%",width:"100%"}}/>
            <InnerHeadings>
              {e.city}
            </InnerHeadings>
            <ColorText >
              {e.text}
    
            </ColorText>
          </SwiperSlide>
        ))}
      </Swiper>
      {/* <div className="swiper-button-next swiper-button-white"></div> */}
      {/* <div className="swiper-button-prev swiper-button-white"></div> */}
    </PlanCard>
  );
}

export default CardSlider;
